<template>
  <!-- 评委介绍 -->
  <div class="huru-recommend">
    <game-info-comp :showAvatarInfo="true" :headerInfo="headerInfo" :contentInfo="contentInfo" v-if="juryInfo" :juryInfo='juryInfo'></game-info-comp>
  </div>
</template>
<script>
import gameInfoComp from '../../../components/gameInfo/gameInfo'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      headerInfo: {
        imgUrl:
          'http://y.gtimg.cn/music/photo_new/T002R300x300M000003d636i2FSP7z.jpg?max_age=2592000',
        name: 'IKON',
        phone: '13458956354'
      },
      contentInfo: {
        title: '评委简介',
        text: `我是评委简介我是评委简介我是评委简介我是评委简介
              我是评委简介我是评委简介我是评委简介我是评委简介
              我是评委简介我是评委简介我是评委简介我是评委简介
              我是评委简介我是评委简介我是评委简介我是评委简介`
      }
    }
  },
  computed: {
    ...mapState({
      juryInfo: state => state.admin.juryDescribe.juryInfo
    })
  },
  async created () {
    const { id } = this.$route.query
    await this.$store.dispatch('admin/juryDescribe/getJuryInfo', id)
    console.log(this.$route, 'router')
  },
  components: {
    gameInfoComp
  }
}
</script>
<style lang="less">
.huru-recommend {
  width: 100%;
  box-sizing: border-box;
  padding: 15px 18px;
}
</style>



